.cardProducts {
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, 0.125);
    font-weight: 300;
    padding: 1rem !important;
    margin-top: 20px;
  }
  .productsTitle {
    font-size: 1.625rem;
    font-weight: 500;
    line-height: 1;
    margin: 0;
    color: #3d5170;
  }
  .headerProducts{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  .btnAddProduct{
    background: #57b846;
    border-color: #57b846;
  }
  /* product details */
  .headingDetails{
    margin-left: 10px;
  }
  .headingDetails .small {
    letter-spacing: 0.125rem;
    color: #818ea3;
    font-size: 0.625rem;
    margin: 0px;
    margin-bottom: 4px;
    text-transform: uppercase;
  }
  .headingDetails .large {
    font-size: 1.625rem;
    font-weight: 500;
    line-height: 1;
    margin: 0;
    margin-bottom: 10px;
    color: #3d5170;
  }
 .productImgCard{
  background-color: #fff;
  font-weight: 300;
  padding: 1rem !important;
  margin-top: 20px;
  border-radius: 0.625rem;
  box-shadow: 0 2px 0 rgb(90 97 105 / 11%), 0 4px 8px rgb(90 97 105 / 12%), 0 10px 10px rgb(90 97 105 / 6%), 0 7px 70px rgb(90 97 105 / 10%);
 }
 .productDetailImg{
   width: 50%;
 }
 .productImgWrapper{
   width: 100%;
   display: flex;
   flex-direction: column;
   align-items: center;
 }
 .productImgWrapper h4{
   margin: 20px 0px ;
   font-weight: 500;
   font-size: 20px;
   color: #3d5170;
 }
 .changePic{
   margin: 15px 0px;
 }
 .divider{
   border-top: 1px solid #d3d3d3;
   margin: 10px 5%;
 }

 .productDetailCard{
  background-color: #fff;
  font-weight: 300;
  margin-top: 20px;
  border-radius: 0.625rem;
  box-shadow: 0 2px 0 rgb(90 97 105 / 11%), 0 4px 8px rgb(90 97 105 / 12%), 0 10px 10px rgb(90 97 105 / 6%), 0 7px 70px rgb(90 97 105 / 10%);
 }
 .productDetailCard .header{
   padding: 1rem;
   display: flex;
   align-items: center;
   border-bottom: 1px solid #d3d3d3;
 }
 .header h4{
   margin: 0px;
   font-weight: 500;
   margin-right: 20px;
 }
 .bodyProductDetail{
   width: 100%;
   padding: 1rem;
 }
 @media (max-width:780px){
  .headingDetails{
   text-align: center;
  }
 }